import React, {useState} from 'react';
import s from './CodeBar.module.scss';
import HtmlEditor from "../Editor/HtmlEditor";
import CssEditor from "../Editor/CssEditor";
import JsEditor from "../Editor/JsEditor";

const CodeBar = () => {
    const [activeTab, setActiveTab] = useState('html');

    return (
        <div className={s.codebar}>
            <nav className={s.tab}>
                <button
                    className={`${s.item} ${activeTab === 'html' ? s.activeTab : ''}`}
                    onClick={() => setActiveTab('html')}
                >HTML</button>
                <button
                    className={`${s.item} ${activeTab === 'css' ? s.activeTab : ''}`}
                    onClick={() => setActiveTab('css')}
                >CSS</button>
                <button
                    className={`${s.item} ${activeTab === 'js' ? s.activeTab : ''}`}
                    onClick={() => setActiveTab('js')}
                >JS</button>
            </nav>
            <div className={s.editor}>
                {activeTab === 'html' ? <HtmlEditor/> : null}
                {activeTab === 'css' ? <CssEditor/> : null}
                {activeTab === 'js' ? <JsEditor/> : null}
            </div>
        </div>
    );
};

export default CodeBar;